  display: block;
  position: relative;
}
.dropify-wrapper.touch-fallback .dropify-preview .dropify-render .dropify-font-file {
  position: relative;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  inset-block-start: 0;
  inset-inline-start: 0;
}
